/* pages
   ========================================================================== */

.c-page {
  @if $enable-rhythm {

    // rhythm preview
    background: linear-gradient(
      to bottom,
      color(whitesmoke),
      color(whitesmoke) 1px,
      color(white) 1px,
      color(white) 50%,
      color(whitesmoke) 50%,
      color(whitesmoke) calc(50% + 1px),
      color(white) calc(50% + 1px),
      color(white)
    );
    background-size: 100% $baseline * 2;
  }
}

// inactive
.c-page.is-inactive {
  overflow: hidden;
}
