/* responsive
   ========================================================================== */

@if $enable-hyphens {
  @media (max-width: bp(sm)) {

    // hyphens
    div,
    textarea,
    table,
    td,
    th,
    code,
    pre,
    samp {
      hyphens: auto;
      word-wrap: break-word;
    }
  }
}
